import tumult from 'tumult';

import Engine from './utils/engine';
// import Engine from './utils/engine3d';

import { distance, color, colourInterpolate, colorToString, clamp, getInterpolatedColours } from './utils/utils';
import { circlePath } from './utils/paths';

const WIDTH = 480;
const HEIGHT = 480;

const el = document.getElementById('canvas');
// const el = document.getElementById('container');

const app = new Engine(el, {
  debug: false,
  width: WIDTH,
  height: HEIGHT,
  loop: false,
  pixelRatio: 1
});

const simplex2 = new tumult.Simplex2();

const bgColor = color(250, 250, 250);

function drawRect(_x, _y, wid, hei, _color, _mixColor) {
  const pixels = ctx.getImageData(_x, _y, wid, hei);
  const id = ctx.createImageData(wid, hei);

  for (let x = 0; x < wid; x++) {
    for (let y = 0; y < hei; y++) {
      const actualBg = color(
        pixels.data[idx],
        pixels.data[idx + 1],
        pixels.data[idx + 2],
      );
      const bg = _mixColor ? _mixColor : actualBg;

      let idx = ((y * wid) + x) * 4;

      const col = Math.random() > 0.75 ? colourInterpolate((Math.random() * 0.25) + 0.1, _color, bg) : _color;

      id.data[idx + 0] = col.r;
      id.data[idx + 1] = col.g;
      id.data[idx + 2] = col.b;
      id.data[idx + 3] = 255;
    }
  }

  ctx.putImageData(id, _x, _y);
}

function drawCircle(_x, _y, radius, _color, _mixColor) {
  let wid = radius * 2;
  let hei = radius * 2;

  const pixels = ctx.getImageData(_x - radius, _y - radius, wid, hei);
  const id = ctx.createImageData(wid, hei);

  for (let x = -radius; x < radius; x++) {
    for (let y = -radius; y < radius; y++) {

      let idx = clamp((((y + radius) * wid) + (x + radius)) * 4, (wid * hei) * 4, 0);

      let rand = simplex2.gen((x + _x) / 5, (y + _y) / 5)
      const actualBg = color(
        pixels.data[idx],
        pixels.data[idx + 1],
        pixels.data[idx + 2],
      );
      const bg = _mixColor ? _mixColor : actualBg;

      let d = distance(x, y, 0, 0);

      const col = rand ? colourInterpolate((Math.random() * 0.1) + 0.1, _color, bg) : _color;

      if (d < radius - 2) {
        id.data[idx + 0] = col.r;
        id.data[idx + 1] = col.g;
        id.data[idx + 2] = col.b;
        id.data[idx + 3] = 255;
      } else if (d < radius) {
        const col = colourInterpolate((Math.random() * 0.05) + 0.1, _color, actualBg);
        id.data[idx + 0] = col.r;
        id.data[idx + 1] = col.g;
        id.data[idx + 2] = col.b;
        id.data[idx + 3] = 255;
      } else {
        id.data[idx + 0] = actualBg.r;
        id.data[idx + 1] = actualBg.g;
        id.data[idx + 2] = actualBg.b;
        id.data[idx + 3] = 255;
      }
    }
  }

  ctx.putImageData(id, _x - radius, _y - radius);
}

const colourArr = getInterpolatedColours([
  color(255, 0, 0),
  color(0, 0, 255),
], 6);

app.onRender(({ frame, width, height, debug }) => {
  const points = circlePath(width / 2, height / 2, width / 6, 6, Math.PI * 1.5);

  ctx.fillStyle = 'white';
  ctx.fillRect(0, 0, width, height);

  drawRect(0, 0, width, height, bgColor, color(200, 200, 200));

  for (let i = points.length - 1; i >= 0; i--) {
    drawCircle(points[i].x, points[i].y, width * 0.2, colourArr[i + 1], bgColor);
  }

  drawCircle(width / 2, height / 2, width * 0.2, colourArr[0], bgColor);
});

app.start();
